l{\n            padding-left: 0;\n            \n            li{\n                list-style: none;\n                \n                .dropdown_option{\n                    padding: 0.75rem 0.75rem;\n                    text-decoration: none;\n                    display: flex;\n                    gap: 12px;\n                    align-items: center;\n                    img{\n                        border-radius: 50%;\n                        aspect-ratio: 1/1;\n                        width: 24px;\n                    }\n                    .language_name{\n                        p{\n                            color: black;\n                            font-family: 'Ovo', serif;\n                        }\n                    }\n                }\n            }\n        }\n        \n    }\n}\n\n.border_bottom{\n    border-bottom: solid 0.8px rgb(207, 207, 207);\n}",".escorts_title{\n    font-size: 3rem;\n    font-weight: lighter;\n    transition: all 0.5s ease;\n}\n\n.escorts_number_title{\n    font-size: 3rem;\n    font-weight: lighter;\n}\n\n.txt_subtitle{\n    font-family: 'Ovo', serif;\n    font-size: 24px;\n}\n\n.section_title{\n    display: inline-block;\n    padding: 1rem 1rem;\n    background-color: white;\n    //Borders\n    border-bottom-right-radius: 100vw;\n    border-top-right-radius: 100vw;\n    //Spacing\n    margin-bottom: 2rem;\n}\n\n.txt_g_center{\n    text-align: center;\n}\n\n.number_title{\n    font-weight: lighter;\n    font-size: 4rem;\n}\n\n.info_title{\n    font-family: 'Ovo', serif;\n    font-size: 3rem;\n}\n\n.owner_text{\n    font-size: 2.5rem;\n}\n\n//this is xsm - extra small\n@media (min-width: 599px) {\n    .txt_xsm_center{\n        text-align: center;\n    }\n\n    .escorts_title_xsm{\n        font-size: 4rem;\n    }\n\n}\n\n//this is sm - small\n@media (min-width: 904px) {\n    .txt_sm_center{\n        text-align: center;\n    }\n\n    .escorts_title_sm{\n        font-size: 4.5rem;\n    }\n}\n\n//this is md - medium\n@media (mi